import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { Teacher } from './teacher';
import { Announcement } from './announcement';
import { Assignment } from './assignment';

@Injectable()
export class IpuGoService {
  private base = 'http://localhost:9000/';
  private headers = new Headers({'Content-Type': 'application/json'});
  private token: string;
  private name: string;

  constructor(private http: Http) { }

  setName(x){
    this.name=x;
  }

  setToken(x){
    this.token=x;
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
  login(user: Teacher): Promise<any> {
    return this.http
      .post(this.base + 'teacher/login', user, {headers: this.headers})
      .toPromise()
      .then(res =>res.json())
      .catch(this.handleError);
  }
  postAssignment(assignment: Assignment): Promise<any>{
    this.headers.append('x-access-token',this.token);
    assignment.name = this.name;
    return this.http
      .post(this.base + 'assignment', assignment, {headers: this.headers})
      .toPromise()
      .then(res => this.headers.delete('x-access-token'))
      .catch(this.handleError);
  }
  postAnnouncement(announcement: Announcement): Promise<Boolean> {
    this.headers.append('x-access-token',this.token);
    announcement.name = this.name;
    return this.http
      .post(this.base + 'announcement', announcement, {headers: this.headers})
      .toPromise()
      .then(res =>this.headers.delete('x-access-token'))
      .catch(this.handleError);
  }

}



