import { Component, OnInit } from '@angular/core';
import { IpuGoService } from '../ipu-go.service';
import { Announcement } from '../announcement';

@Component({
  selector: 'app-announcement',
  templateUrl: './announcement.component.html',
  styleUrls: ['./announcement.component.css']
})
export class AnnouncementComponent implements OnInit {
  announcement: Announcement = {name:'',sem:'',group:'',message:''};
  constructor(private ipuService: IpuGoService) { }

  ngOnInit() {
  }
    post(){
      let success=false;
      this.ipuService.postAnnouncement(this.announcement).then(
        res =>alert('Announcement posted successfully'),
        err=>alert('Announcement could not be posted'))
    }

}



