import { Component, OnInit } from '@angular/core';
import { Assignment } from '../assignment';
import { IpuGoService } from '../ipu-go.service';
declare var jquery:any;
declare var $ :any;

@Component({
  selector: 'app-assignment',
  templateUrl: './assignment.component.html',
  styleUrls: ['./assignment.component.css']
})
export class AssignmentComponent implements OnInit {
  assignment: Assignment = {name:'',sem:'',group:'',subject:'',marks:'',last:'',files:['']};
  file: Object;
  constructor(private ipuService: IpuGoService) { }

  ngOnInit() {
  }
  uploadFile(file, signedRequest, url){
      const xhr = new XMLHttpRequest();
      xhr.open('PUT', signedRequest);
      xhr.onreadystatechange = () => {
        if(xhr.readyState === 4){
          if(xhr.status === 200){
            this.assignment.files.push(url);
          }
          else{
            alert('Could not upload file.');
          }
        }
      };
      xhr.send(file);
    }
    getSignedRequest(file){
      const xhr = new XMLHttpRequest();
      xhr.open('GET', `http://localhost:9000/assignment/sign-s3?file-name=${file.name}&file-type=${file.type}`);
      xhr.onreadystatechange = () => {
        if(xhr.readyState === 4){
          if(xhr.status === 200){
            const response = JSON.parse(xhr.responseText);
            this.uploadFile(file, response.signedRequest, response.url);
          }
          else{
            alert('Could not get signed URL.');
          }
        }
      };
      xhr.send();
    }
    post(){
        this.ipuService.postAssignment(this.assignment).then(res=>
        alert('Assignment posted successfully'),
        err=> alert('Assignment could not be uploaded'));
    }
  public fileChangeEvent(fileInput: any){
      if (fileInput.target.files && fileInput.target.files[0]) {
          let files = fileInput.target.files;
          let i;
          for(i=0; i<files.length; i++){
              this.getSignedRequest(files[i]);
          }
          if(i==files.length)
            alert('Image Uploading is now complete');
        }
    }

}



